.container {
	position: relative;
	top: 0;
	left: 0;
	width: 250px;
	height: 350px;
}

.photo {
	width: 100%;
	height: 100%;
	border-radius: var(--border-radius-medium);
	object-fit: cover;
	object-position: center center;
}

.favorite {
	position: absolute;
	width: 35px;
	height: 35px;
	top: -15px;
	right: -15px;
	object-fit: cover;
	object-position: center center;
	cursor: pointer;
	filter: drop-shadow(0px 0px 2px #000);
}
